<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="6"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <router-link to="/account">
                <li class="active">账户中心</li>
              </router-link>
              <router-link to="/investorManage">
                <li>投资者管理</li>
              </router-link>
              <router-link to="/leaveManage">
                <li>留言管理</li>
              </router-link>
            </ul>
          </div>
          <div class="centerContent">
            <div class="row">
              <label class="title">公司logo</label>
              <div class="txBox">
                <img v-if="imageUrl" class="avatar" :src="imageUrl" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                <input class="uploadBtn" type="file" name="image" accept="image/*" @change='chooseImg'>
              </div>
              <p class="clear"></p>
              <p class="beizhu">*仅支持JPG、GIF、PNG、JPEG、BMP格式 , 文件小于2M</p>
            </div>
            <div class="row clearfix">
              <label class="title">企业名称</label>
              <input class="textInp" v-model="cname" type="text" placeholder="请输入企业名称">
            </div>
            <div class="row clearfix">
              <label class="title">手机号码</label>
              <p class="desc">18333156487</p>
            </div>
            <div class="row clearfix">
              <label class="title">品牌名</label>
              <input class="textInp" v-model="bname" type="text" placeholder="请输入品牌名">
            </div>
            <div class="row clearfix">
              <label class="title">注册地址</label>
              <input class="textInp" v-model="address" type="text" placeholder="请输入注册地址">
            </div>
            <div class="row clearfix">
              <label class="title">联系邮箱</label>
              <input class="textInp" v-model="email" type="text" placeholder="请输入联系邮箱">
            </div>
<!--            <div class="keepBtn" >保存</div>-->
            <el-button type="primary" class="keepBtn" @click="getKeep">保存</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from "@/api/account-api"
  export default {
    name: "index.vue",
    data(){
      return{
        data:'',
        imageUrl: '',
        cname:'',
        bname:'',
        address:'',
        email:'',
        logo:'',
      }
    },
    created(){
      this.getData();
    },
    methods: {
      getData(){
        api.getUserData().then(data => {
          console.log(data)
          this.data = data;
          this.imageUrl = data.logo;
          this.cname = data.cname;
          this.bname = data.bname;
          this.address = data.address;
          this.email = data.email;
        });
      },
      chooseImg (e) {
        var file = e.target.files[0]
        console.log(file)
        let imgSize = file.size
        console.log(imgSize)
        var _this = this
        if (imgSize <= 2 * 1024 * 1024) {
          console.log('大小合适')
          var reader = new FileReader();
          reader.readAsDataURL(file); // 读出 base64
          reader.onloadend = function () {
            var dataURL = reader.result;
            api.getUploadImg({type:1,file:dataURL}).then(data => {
              _this.imageUrl = dataURL
              _this.logo = data
            })
          }
        } else {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
      },
      getKeep:function(){
        const {logo,cname,bname,address,email} = this;
        api.changeData({logo,cname,bname,address,email}).then(data => {
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.$router.push("/account");
        })
      },
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
          margin-right: 50px;
        }
        li.active{
          border-bottom: 2px solid #4F74F4;
        }
      }
    }
    .centerContent{
      padding: 35px 0 0;
      .row{
        margin-bottom: 15px;
        .title{
          float: left;
          width: 100px;
          line-height: 40px;
          text-align: right;
          margin-right: 15px;
        }
        .txBox{
          float: left;
          width: 94px;
          height: 94px;
          border: 1px solid #E3E4E5;
          position: relative;
          img{
            display: block;
            width: 94px;
            height: 94px;
          }
          i{
            color: #E1E3EB;
          }
          .uploadBtn{
            width: 94px;
            height: 94px;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
          }
        }
        .beizhu{
          font-size: 12px;
          color: #999;
          margin: 15px 0 34px;
          padding-left: 117px;
        }
        .textInp{
          width: 338px;
          height: 38px;
          line-height: 38px;
          border: 1px solid #E3E4E5;
          border-radius:2px;
          text-indent: 15px;
        }
        .sex{
          height: 40px;
          line-height: 40px;
        }
        .desc{
          float: left;
          height: 40px;
          line-height: 40px;
          color: #000;
        }
      }
      .keepBtn{
        margin: 30px 0 0 230px;
      }

    }
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 94px;
    height: 94px;
    line-height: 94px;
    text-align: center;
  }
</style>



