/**
 * 引入fetch、baseUrl
 * @param params
 * @returns {*}
 */
import httpUtils from 'config/index'

class accountService {
  getUserData(){
    return httpUtils.get("/index/user/user_center");
  }
  getUploadImg(params){
    return httpUtils.get("/index/user/upload_image",params);
  }
  changeData(params){
    return httpUtils.get("/index/user/change_ent_data",params);
  }
}
export default new accountService()



